<template>
    <div class="row">
        <div class="col-md-12">
            <div class="card">
                <div class="card-header">
                    <div class="d-flex align-items-center">
                        <h5>
                            <a class="btn-link" href="#">Lista de Oficios</a>
                        </h5>
                        <button
                            type="button"
                            class="btn btn-sm btn-primary ml-auto px-4"
                            @click="abrirModal(1)"
                        >
                            Nuevo Oficio
                        </button>
                    </div>
                </div>
                <div class="card-body">
                    <p>
                        Este expediente tiene {{ oficios.length }} oficios
                        asignados.
                    </p>
                    <div class="table-responsive">
                        <table
                            class="table"
                            id="table"
                            ref="dataTable1"
                            aria-describedby="table-oficios"
                        >
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Nº Correlativo</th>
                                    <th>Asunto</th>
                                    <th>Fecha de Envío</th>
                                    <th>Destinatario</th>
                                    <th>Acciones</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr
                                    v-for="(oficio, index) in oficios"
                                    :key="index"
                                >
                                    <td>{{ index + 1 }}</td>
                                    <td>{{ oficio.n_correlativo }}</td>
                                    <td>{{ oficio.asunto }}</td>
                                    <td>{{ oficio.fecha_envio }}</td>
                                    <td>{{ oficio.destinatario }}</td>
                                    <td>
                                        <button
                                            class="btn btn-sm btn-primary ml-auto px-4"
                                            @click="cargarDatosOficio(oficio)"
                                        >
                                            Editar
                                        </button>
                                        <span class="mx-1"></span>
                                        <button
                                            class="btn btn-sm btn-danger ml-auto px-4"
                                            @click="deleteOficio(oficio.id)"
                                        >
                                            Eliminar
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>

        <!-- Modal Nuevo Oficio -->
        <div v-if="showModal === 1" class="modal" @click="cerrarModal">
            <div class="modal-content border-0" @click.stop>
                <div class="modal-header bg-primary border-0">
                    <h5 class="modal-title text-white">
                        Registrar nuevo Oficio
                    </h5>
                </div>
                <div class="modal-body">
                    <div class="mb-3">
                        <label class="form-label">Asunto</label>
                        <input
                            type="text"
                            v-model="oficio.asunto"
                            class="form-control"
                        />
                    </div>
                    <div class="mb-3">
                        <label class="form-label">Fecha de Envío</label>
                        <input
                            type="date"
                            v-model="oficio.fecha_envio"
                            class="form-control"
                        />
                    </div>
                    <div class="mb-3">
                        <label class="form-label">Destinatario</label>
                        <input
                            type="text"
                            v-model="oficio.destinatario"
                            class="form-control"
                        />
                    </div>
                </div>
                <div class="modal-footer">
                    <button
                        type="button"
                        class="btn btn-lg btn-secondary"
                        @click="cerrarModal()"
                    >
                        Cerrar
                    </button>
                    <button
                        type="button"
                        class="btn btn-lg btn-primary"
                        @click="createOficios()"
                    >
                        Guardar
                    </button>
                </div>
            </div>
        </div>

        <!-- Modal Editar Oficio -->
        <div v-if="showModal === 2" class="modal" @click="cerrarModal">
            <div class="modal-content border-0" @click.stop>
                <div class="modal-header bg-primary border-0">
                    <h5 class="modal-title text-white">Editar Oficio</h5>
                </div>
                <div class="modal-body">
                    <div class="mb-3">
                        <label class="form-label">Asunto</label>
                        <input
                            type="text"
                            v-model="oficio.asunto"
                            class="form-control"
                        />
                    </div>
                    <div class="mb-3">
                        <label class="form-label">Fecha de Envío</label>
                        <input
                            type="date"
                            v-model="oficio.fecha_envio"
                            class="form-control"
                        />
                    </div>
                    <div class="mb-3">
                        <label class="form-label">Destinatario</label>
                        <input
                            type="text"
                            v-model="oficio.destinatario"
                            class="form-control"
                        />
                    </div>
                </div>
                <div class="modal-footer">
                    <button
                        type="button"
                        class="btn btn-lg btn-secondary"
                        @click="cerrarModal()"
                    >
                        Cerrar
                    </button>
                    <!-- Cambia el tipo de botón a submit para que active el método updateOficio -->
                    <button
                        type="submit"
                        class="btn btn-lg btn-primary"
                        @click="updateOficio()"
                    >
                        Guardar
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import DataTable from "@/utils/dataTable";
import axios from "axios";
import store from "@/store"; // Importa tu tienda Vuex aquí

export default {
    mixins: [DataTable],

    data() {
        return {
            idProceeding: 0,
            cardOficios: false,
            showModal: 0,
            oficio: {
                n_correlativo: "",
                asunto: "",
                fecha_envio: "",
                destinatario: "",
            },
            oficios: [],
        };
    },

    created() {
        this.idProceeding = this.$route.params.id;
    },

    methods: {
        async cargarDetallesOficios() {
            try {
                this.$showLoadingAlert();
                const response = await this.$getData(
                    `proceeding/${this.idProceeding}`,
                    this,
                );
                this.oficios = response.office;

                this.$closeLoadingAlert();
                console.log(this.oficios);
            } catch (error) {
                this.$handleApiError(
                    error,
                    "cargar detalles del expediente ",
                    this.id,
                );
            }
        },

        async createOficios() {
            try {
                // Realizar la solicitud HTTP para crear un nuevo oficio
                this.oficio.expediente_id = this.idProceeding;
                console.log(this.oficio);

                const response = await this.$postData(
                    "proceeding-oficios",
                    this,
                    this.oficio,
                );
                console.log(response); // Loguear la respuesta completa
                this.cargarDetallesOficios();
                this.showModal = 0;
                // Manejar la respuesta del servidor
                if (response.status === 201) {
                    // Agregar el nuevo oficio a la lista de oficios
                    //this.oficios.push(response.data);

                    // Limpiar el formulario
                    this.oficio = {
                        correlativo: "",
                        asunto: "",
                        fecha_envio: "",
                        destinatario: "",
                    };

                    // Cerrar el modal solo si la acción fue exitosa
                    this.showModal = 0;
                    this.cerrarModal(); // Forzar el cierre del modal
                    console.log("respuesta: " + response.status);
                } else {
                    console.log(
                        "La solicitud HTTP no devolvió un código de estado 201",
                    );
                    console.log("Respuesta del servidor:", response);
                }
            } catch (error) {
                // Manejar errores de la solicitud HTTP
                console.error("Error al agregar el oficio:", error);
                console.log("Mensaje de error:", error.message);
                console.log("Respuesta del servidor:", error.response);
            }
        },

        async deleteOficio(idOficio) {
            try {
                this.$showLoadingAlert();
                const response = await this.$deleteData(
                    `proceeding-oficios/${idOficio}`,
                    this,
                );
                console.log(response); // Loguear la respuesta completa
                // Manejar la respuesta del servidor

                this.$closeLoadingAlert();
                this.cargarDetallesOficios();
            } catch (error) {
                // Manejar errores de la solicitud HTTP
                console.error("Error al eliminar el oficio:", error);
                console.log("Mensaje de error:", error.message);
                console.log("Respuesta del servidor:", error.response);
            }
        },

        cargarDatosOficio(oficio) {
            // Verifica si el objeto oficio no está vacío o undefined
            if (oficio) {
                // Asigna los datos del oficio seleccionado al objeto "oficio"
                this.oficio = oficio;
                // Muestra el modal de edición
                this.abrirModal(2);
            } else {
                console.error("Error: el objeto oficio está vacío o undefined");
            }
        },

        async updateOficio() {
            try {
                if (!this.oficio) {
                    console.error('El objeto "oficio" no está definido.');
                    return;
                }

                this.$showLoadingAlert();
                const response = await this.$putData(
                    `proceeding-oficios/${this.oficio.id}`,
                    this,
                    this.oficio,
                );
                console.log(response); // Loguear la respuesta completa
                // Manejar la respuesta del servidor

                // Actualizar la información del oficio en la lista de oficios
                const index = this.oficios.findIndex(
                    (oficio) => oficio.id === this.oficio.id,
                );
                if (index !== -1) {
                    this.$set(this.oficios, index, response.data);
                    console.log("Oficio editado exitosamente");
                }
                this.cerrarModal(); // Cerrar modal después de editar

                this.$closeLoadingAlert();
                this.cargarDetallesOficios();
            } catch (error) {
                // Manejar errores de la solicitud HTTP
                console.error("Error al editar el oficio:", error);
                console.log("Mensaje de error:", error.message);
                console.log("Respuesta del servidor:", error.response);
            }
        },

        toggleCardOficios() {
            this.cardOficios = !this.cardOficios;
        },

        abrirModal(showModal) {
            this.showModal = showModal;
        },

        cerrarModal() {
            this.showModal = 0;
        },
    },

    mounted() {
        this.cargarDetallesOficios();
    },
};
</script>

<style scoped>
/* Aquí puedes agregar estilos CSS específicos para este componente si es necesario */
</style>
